.App {
  text-align: center;
}

.profile_pic {
  border: 3px solid black;
  padding: 2px;
  margin: 25px;
  width: 300px;
  height: 335px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.flex_div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.link_icon {
  display: inline-block;
  width: 50px;
}

.intro_text {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  line-height: 1.5em;
}
.intro_text h2 {
  font-size: 2em;
}
.intro_text h1 {
  font-size: 3em;
}

.base_text_center {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}

.base_text_left{
  font-family: Arial, Helvetica, sans-serif;
  text-align: left;
}

.exp_list_header{
  font-family: Arial, Helvetica, sans-serif;
  text-align: left;
  padding-top: 30px;
  padding-left: 5%;
}

.column_grid{
  justify-content: center;
  text-align: center;
  display: grid;
  width:100%;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}

.grid_one{
  grid-column: 1;
  text-align: left;
  padding-left: 25%;
}

.grid_two{
  grid-column: 2;
  text-align: left;
  padding-left: 25%;
}

.grid_three{
  grid-column: 3;
  text-align: left;
  padding-left: 25%;
}
.grid_four{
  grid-column: 4;
  text-align: left;
  padding-left: 25%;
}

.projects{
  text-align: left;
  padding-left: 5%;
  padding-right: 5%;
}
.project_desc{
  border: 2px solid rgb(161, 159, 159);
  border-radius: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.contact{
  margin-top: 20px;
  margin-bottom: 20px;
}